import React from "react";
import ApplicationComponent from "../common/applicationComponent";
import View from "../common/view";
import H1 from "../common/h1";
import styleSchema from "../common/styles";
import GitHubIcon from "@material-ui/icons/GitHub";
import LinkedInIcon from "@material-ui/icons/LinkedIn";
import DescriptionIcon from "@material-ui/icons/Description";
import Button from "@material-ui/core/Button";
import H2 from "../common/h2";

export default class MainContent extends ApplicationComponent {
  render() {
    return (
      <View style={styles.roortContainer}>
        <View>
          <p style={styles.name}>Ka Kei Pun</p>
        </View>
        <H1 color="grey" style={styles.pageUnderConstructionDescription}>
          {this.labels.landingPage.pageUnderConstruction}
        </H1>
        <this.RedirectButtons />
      </View>
    );
  }

  RedirectButtons = () => {
    const buttons = this.labels.landingPage.redirect.map((redirect) => {
      return (
        <Button href={redirect.redirectUrl}>
          <this.Icon type={redirect.type} />
          <H2 color="white">{redirect.label}</H2>
        </Button>
      );
    });
    return <View style={styles.buttonsContainer}>{buttons}</View>;
  };

  getIcon(type, style) {
    switch (type) {
      case "github":
        return <GitHubIcon style={style} />;
      case "linkedin":
        return <LinkedInIcon style={style} />;
      case "resume":
        return <DescriptionIcon style={style} />;
      default:
        return null;
    }
  }

  Icon = ({ type }) => {
    return <>{this.getIcon(type, styles.icon)}</>;
  };
}

const styles = {
  buttonsContainer: {
    alignSelf: "center",
    marginTop: 20,
    width: "minContent",
  },
  icon: {
    color: styleSchema.color.white,
    fontSize: 38,
    marginRight: 8,
  },
  name: {
    color: styleSchema.color.white,
    fontSize: 38,
    fontWeight: 300,
    margin: 0,
  },
  pageUnderConstructionDescription: {
    marginTop: 20,
  },
  roortContainer: {
    backgroundColor: styleSchema.color.primary,
    borderRadius: 15,
    flexDirection: "column",
    padding: 15,
    width: "inherit",
  },
};
