import React, { Component } from "react";
import AppStateContext from "./appStateContext";

export default class AppState extends Component {
  state = {
    darkMode: false,
  };

  render() {
    return (
      <div>
        <AppStateContext.Provider
          value={{
            darkMode: this.state.darkMode,
          }}
        >
          {this.props.children}
        </AppStateContext.Provider>
      </div>
    );
  }
}
