import React, { Component } from "react";
import Box from "@material-ui/core/Box";

export default class View extends Component {
  render() {
    return (
      <Box style={{ ...styles.defaultStyle, ...this.props.style }}>
        {this.props.children}
      </Box>
    );
  }
}

const styles = {
  defaultStyle: {
    display: "flex",
  },
};
