import React from "react";
import ApplicationComponent from "./applicationComponent";
import styleSchema from "./styles";

export default class H1 extends ApplicationComponent {
  render() {
    const color = {
      color: this.getColor(this.props.color),
    };
    return (
      <p style={{ ...this.getStyle(), ...color, ...this.props.style }}>
        {this.props.children}
      </p>
    );
  }

  getColor(colorInput) {
    switch (colorInput) {
      case "white":
        return styleSchema.color.white;
      case "black":
        return styleSchema.color.black;
      case "grey":
        return styleSchema.color.greyDark;
      default:
        return styleSchema.color.primary;
    }
  }

  getStyle() {
    return styles.defaultStyle;
  }
}

const styles = {
  defaultStyle: {
    fontSize: 18,
    fontWeight: 600,
    margin: 0,
    padding: 0,
  },
};
